.simplebox-title {
    margin: 0 0 8px;
    padding: 5px 8px;
}
.simplebox-content {
    padding: 0 8px;
}
.simplebox.align-right {
    float: right;
}
.simplebox.align-left {
    float: left;
}
.simplebox.align-center {
    margin-left: auto;
    margin-right: auto;
}

.format--content img{
    margin:0 1px 0 1px;
}

