.similer {
    position: relative;
    width: 100%;
    height: 100%;
    padding-bottom: 23px;
}
.divsimiler>:first-child:after {
    display: block;
    content: "";
    width: 100%;
    height: 50px;
    position: absolute;
    right: -10px;
    bottom: -5px;
    border-right: 2px solid red;
    border-bottom: 2px solid red;
}
.divsimiler>:first-child:before {
    display: block;
    content: "";
    width: 100%;
    height: 50px;
    position: absolute;
    bottom: -5px;
    left: -7px;
    border-bottom: 2px solid red;
    border-left:2px solid red;
}

@media(max-width : 768px){
    .divsimiler>:first-child:after {
        right: 0 !important;
    }
    .divsimiler>:first-child:before {
